    // –£–∫–∞–∑—ã–≤–∞–µ–º –∞–¥–∞–ø—Ç–∏–≤–Ω—ã–µ —Ä–∞–∑–º–µ—Ä—ã –∫–æ–ª–æ–Ω–æ–∫:
    // xs={24}  -> –±—É–¥–µ—Ç –∑–∞–Ω–∏–º–∞—Ç—å –≤—Å—é —à–∏—Ä–∏–Ω—É –Ω–∞ —ç–∫—Ä–∞–Ω–∞—Ö < 576px
    // sm={12}  -> –ø–æ–ª–æ–≤–∏–Ω—É —à–∏—Ä–∏–Ω—ã –æ—Ç 576px –¥–æ 768px
    // md={8}   -> —Ç—Ä–µ—Ç—å —à–∏—Ä–∏–Ω—ã –æ—Ç 768px –∏ –≤—ã—à–µ
