/**
 * –§—É–Ω–∫—Ü–∏—è —Å–æ—Ö—Ä–∞–Ω—è–µ—Ç —Å–ø–∏—Å–æ–∫ –Ω–æ–≤–æ—Å—Ç–µ–π –≤ localStorage –ø–æ –∫–ª—é—á—É NEWS_KEY.
 * @param {Array} list - –º–∞—Å—Å–∏–≤ –Ω–æ–≤–æ—Å—Ç–µ–π
 */
function saveNewsList(list) {
  localStorage.setItem(NEWS_KEY, JSON.stringify(list))
}

/**
 * –ü–æ–ª—É—á–∏—Ç—å –≤—Å–µ –Ω–æ–≤–æ—Å—Ç–∏ –∏–∑ localStorage.
 * –ï—Å–ª–∏ —Ç–∞–º –Ω–∏—á–µ–≥–æ –Ω–µ—Ç, –∏—Å–ø–æ–ª—å–∑—É–µ—Ç—Å—è –º–∞—Å—Å–∏–≤ initialNews,
 * –∫–æ—Ç–æ—Ä—ã–π –∑–∞–ø–∏—Å—ã–≤–∞–µ—Ç—Å—è –≤ localStorage –∏ –∑–∞—Ç–µ–º –≤–æ–∑–≤—Ä–∞—â–∞–µ—Ç—Å—è.
 * @returns {Array} —Å–ø–∏—Å–æ–∫ –Ω–æ–≤–æ—Å—Ç–µ–π
 */
export function getAllNews() {
  const data = localStorage.getItem(NEWS_KEY)
  if (!data) {
    saveNewsList(initialNews)
    return initialNews
  }
  try {
    return JSON.parse(data)
  } catch (e) {
    console.error('–û—à–∏–±–∫–∞ –ø–∞—Ä—Å–∏–Ω–≥–∞ news –∏–∑ localStorage', e)
    return []
  }
}

/**
 * –°–æ–∑–¥–∞—Ç—å –Ω–æ–≤–æ—Å—Ç—å –∏ —Å–æ—Ö—Ä–∞–Ω–∏—Ç—å –≤ localStorage.
 * @param {Object} newsData - –¥–∞–Ω–Ω—ã–µ –Ω–æ–≤–æ—Å—Ç–∏
 * @returns {Object} —Å–æ–∑–¥–∞–Ω–Ω–∞—è –Ω–æ–≤–æ—Å—Ç—å (—Å –ø—Ä–∏—Å–≤–æ–µ–Ω–Ω—ã–º id)
 */
export function createNews(newsData) {
  const list = getAllNews()
  const newId = String(Date.now()) // –ø—Ä–æ—Å—Ç–æ–µ —É–Ω–∏–∫–∞–ª—å–Ω–æ–µ –∑–Ω–∞—á–µ–Ω–∏–µ
  const newPost = {
    id: newId,
    ...newsData,
  }
  // –î–æ–±–∞–≤–∏–º –Ω–æ–≤–æ—Å—Ç—å –≤ –Ω–∞—á–∞–ª–æ —Å–ø–∏—Å–∫–∞:
  list.unshift(newPost)
  saveNewsList(list)
  return newPost
}

/**
 * –û–±–Ω–æ–≤–∏—Ç—å —Å—É—â–µ—Å—Ç–≤—É—é—â—É—é –Ω–æ–≤–æ—Å—Ç—å.
 * @param {string} id - –∏–¥–µ–Ω—Ç–∏—Ñ–∏–∫–∞—Ç–æ—Ä –Ω–æ–≤–æ—Å—Ç–∏
 * @param {Object} updates - –ø–æ–ª—è, –∫–æ—Ç–æ—Ä—ã–µ –Ω—É–∂–Ω–æ –∏–∑–º–µ–Ω–∏—Ç—å
 * @returns {Object|null} –æ–±–Ω–æ–≤–ª—ë–Ω–Ω–∞—è –Ω–æ–≤–æ—Å—Ç—å –∏–ª–∏ null, –µ—Å–ª–∏ –Ω–µ –Ω–∞–π–¥–µ–Ω–∞
 */
export function updateNews(id, updates) {
  const list = getAllNews()
  const index = list.findIndex((item) => item.id === id)
  if (index === -1) {
    console.warn(`–ù–æ–≤–æ—Å—Ç—å —Å id=${id} –Ω–µ –Ω–∞–π–¥–µ–Ω–∞`)
    return null
  }
  list[index] = { ...list[index], ...updates }
  saveNewsList(list)
  return list[index]
}

/**
 * –£–¥–∞–ª–∏—Ç—å –Ω–æ–≤–æ—Å—Ç—å –ø–æ –µ—ë id.
 * @param {string} id - –∏–¥–µ–Ω—Ç–∏—Ñ–∏–∫–∞—Ç–æ—Ä –Ω–æ–≤–æ—Å—Ç–∏
 */
export function deleteNews(id) {
  let list = getAllNews()
  list = list.filter((item) => item.id !== id)
  saveNewsList(list)
}
