import React, { useEffect, useState } from 'react'

import { PlusOutlined } from '@ant-design/icons'
import { Button, Card, Form, Input, Modal, Upload, notification } from 'antd'

import { createNews, deleteNews, getAllNews, updateNews } from '../../../entities/news/model/newsStorage'
import { getSession } from '../../../features/auth/model/session'

import './NewsPage.scss'
import { getBase64 } from './utils'

export const NewsPage = () => {
  const user = getSession() // { login, role, fullName, ... }
  const [newsList, setNewsList] = useState([])
  const [isCreateModalOpen, setIsCreateModalOpen] = useState(false)
  const [createForm] = Form.useForm()
  const [isEditModalOpen, setIsEditModalOpen] = useState(false)
  const [editForm] = Form.useForm()
  const [editingNewsId, setEditingNewsId] = useState(null)

  useEffect(() => {
    const list = getAllNews()
    setNewsList(list)
  }, [])

  // –û–¢–ö–†–´–¢–¨/–ó–ê–ö–†–´–¢–¨ —Å–æ–∑–¥–∞–Ω–∏–µ
  const openCreateModal = () => {
    setIsCreateModalOpen(true)
    createForm.resetFields()
  }

  const handleCancelCreate = () => {
    setIsCreateModalOpen(false)
    createForm.resetFields()
  }

  // –û–¢–ö–†–´–¢–¨/–ó–ê–ö–†–´–¢–¨ —Ä–µ–¥–∞–∫—Ç–∏—Ä–æ–≤–∞–Ω–∏–µ
  const openEditModal = (post) => {
    setEditingNewsId(post.id)
    setIsEditModalOpen(true)

    // –ü–æ–¥—Å—Ç–∞–≤–ª—è–µ–º –Ω–∞—á–∞–ª—å–Ω—ã–µ –∑–Ω–∞—á–µ–Ω–∏—è –≤ —Ñ–æ—Ä–º—É
    editForm.setFieldsValue({
      title: post.title,
      content: post.content,
      // –î–ª—è Upload –Ω—É–∂–Ω–æ —Å—Ñ–æ—Ä–º–∏—Ä–æ–≤–∞—Ç—å –º–∞—Å—Å–∏–≤ fileList
      image: post.image
        ? [
            {
              uid: '-1',
              name: 'news-image',
              status: 'done',
              url: post.image, // base64 –ª–∏–±–æ –∫–∞–∫–∞—è-—Ç–æ —Å—Å—ã–ª–∫–∞
            },
          ]
        : [],
    })
  }

  const handleCancelEdit = () => {
    setIsEditModalOpen(false)
    setEditingNewsId(null)
    editForm.resetFields()
  }

  // -------------------------------
  // –°–û–ó–î–ê–ù–ò–ï
  // -------------------------------
  // –í—ã–∑—ã–≤–∞–µ—Ç—Å—è –ø—Ä–∏ —Å–∞–±–º–∏—Ç–µ —Ñ–æ—Ä–º—ã "–°–æ–∑–¥–∞—Ç—å"
  const handleCreateFinish = async (values) => {
    try {
      // –ü–æ–ª—É—á–∏–º Base64-–∏–∑–æ–±—Ä–∞–∂–µ–Ω–∏–µ –∏–∑ Upload (–µ—Å–ª–∏ –∑–∞–≥—Ä—É–∂–µ–Ω–æ)
      let imageBase64 = ''
      if (values.image && values.image.length > 0) {
        const fileObj = values.image[0].originFileObj
        imageBase64 = await getBase64(fileObj)
      }

      const newPost = createNews({
        author: user.fullName || user.login,
        role: user.role,
        title: values.title,
        content: values.content,
        createdAt: new Date().toISOString(),
        image: imageBase64,
      })
      setNewsList((prev) => [newPost, ...prev])
      notification.success({ message: '–ù–æ–≤–æ—Å—Ç—å –æ–ø—É–±–ª–∏–∫–æ–≤–∞–Ω–∞!' })
      handleCancelCreate()
    } catch (error) {
      notification.error({ message: '–û—à–∏–±–∫–∞ –ø—Ä–∏ —Å–æ–∑–¥–∞–Ω–∏–∏ –Ω–æ–≤–æ—Å—Ç–∏' })
      console.error(error)
    }
  }

  // -------------------------------
  // –†–ï–î–ê–ö–¢–ò–†–û–í–ê–ù–ò–ï
  // -------------------------------
  // –í—ã–∑—ã–≤–∞–µ—Ç—Å—è –ø—Ä–∏ —Å–∞–±–º–∏—Ç–µ —Ñ–æ—Ä–º—ã "–†–µ–¥–∞–∫—Ç–∏—Ä–æ–≤–∞—Ç—å"
  const handleEditFinish = async (values) => {
    try {
      let imageBase64 = ''
      if (values.image && values.image.length > 0) {
        const fileObj = values.image[0].originFileObj
        imageBase64 = await getBase64(fileObj)
      }

      const updated = updateNews(editingNewsId, {
        title: values.title,
        content: values.content,
        image: imageBase64,
      })
      setNewsList((prev) => prev.map((item) => (item.id === editingNewsId ? updated : item)))
      notification.success({ message: '–ù–æ–≤–æ—Å—Ç—å –æ—Ç—Ä–µ–¥–∞–∫—Ç–∏—Ä–æ–≤–∞–Ω–∞!' })
      handleCancelEdit()
    } catch (error) {
      notification.error({ message: '–û—à–∏–±–∫–∞ –ø—Ä–∏ —Ä–µ–¥–∞–∫—Ç–∏—Ä–æ–≤–∞–Ω–∏–∏ –Ω–æ–≤–æ—Å—Ç–∏' })
      console.error(error)
    }
  }

  // -------------------------------
  // –£–î–ê–õ–ï–ù–ò–ï
  // -------------------------------
  const handleDelete = (postId) => {
    try {
      deleteNews(postId)
      setNewsList((prev) => prev.filter((item) => item.id !== postId))
      notification.success({ message: '–ù–æ–≤–æ—Å—Ç—å —É–¥–∞–ª–µ–Ω–∞!' })
    } catch (error) {
      notification.error({ message: '–û—à–∏–±–∫–∞ –ø—Ä–∏ —É–¥–∞–ª–µ–Ω–∏–∏ –Ω–æ–≤–æ—Å—Ç–∏' })
      console.error(error)
    }
  }

  // –§—É–Ω–∫—Ü–∏—è –¥–ª—è —É–¥–æ–±–Ω–æ–≥–æ –ø—Ä–µ–æ–±—Ä–∞–∑–æ–≤–∞–Ω–∏—è Upload-—Å–æ–±—ã—Ç–∏—è –≤ –Ω—É–∂–Ω—ã–π —Ñ–æ—Ä–º–∞—Ç
  const normFile = (e) => {
    if (Array.isArray(e)) {
      return e
    }
    return e?.fileList
  }

  return (
    <div className="newsContainer">
      <h2>–ù–æ–≤–æ—Å—Ç–∏</h2>

      {user?.role === 'teacher' && (
        <Button type="primary" style={{ marginBottom: 16 }} onClick={openCreateModal}>
          –°–æ–∑–¥–∞—Ç—å –Ω–æ–≤–æ—Å—Ç—å
        </Button>
      )}

      <div className="newsList">
        {newsList.map((post) => (
          <div className="newsCard" key={post.id}>
            <Card>
              <div className="newsMeta content">
                –ê–≤—Ç–æ—Ä: <b>{post.author}</b> | {new Date(post.createdAt).toLocaleString()}
              </div>
              {post.image && (
                <div>
                  <img src={post.image} alt="–∏–∑–æ–±—Ä–∞–∂–µ–Ω–∏–µ –Ω–æ–≤–æ—Å—Ç–∏" className="newsImage" />
                </div>
              )}

              <div className="content">
                <div className="newsTitle">{post.title}</div>
                <div className="newsContent">{post.content}</div>

                {user?.role === 'teacher' && (
                  <div style={{ textAlign: 'right', marginTop: 8 }}>
                    <Button type="link" onClick={() => openEditModal(post)} style={{ marginRight: 8 }}>
                      –†–µ–¥–∞–∫—Ç–∏—Ä–æ–≤–∞—Ç—å
                    </Button>
                    <Button danger type="link" onClick={() => handleDelete(post.id)}>
                      –£–¥–∞–ª–∏—Ç—å
                    </Button>
                  </div>
                )}
              </div>
            </Card>
          </div>
        ))}
      </div>

      {/* –ú–æ–¥–∞–ª–∫–∞: –°–æ–∑–¥–∞–Ω–∏–µ */}
      <Modal
        title="–°–æ–∑–¥–∞—Ç—å –Ω–æ–≤–æ—Å—Ç—å"
        open={isCreateModalOpen}
        onCancel={handleCancelCreate}
        onOk={() => createForm.submit()}
      >
        <Form form={createForm} layout="vertical" onFinish={handleCreateFinish} name="createNewsForm">
          <Form.Item label="–ó–∞–≥–æ–ª–æ–≤–æ–∫" name="title" rules={[{ required: true, message: '–í–≤–µ–¥–∏—Ç–µ –∑–∞–≥–æ–ª–æ–≤–æ–∫ –Ω–æ–≤–æ—Å—Ç–∏' }]}>
            <Input />
          </Form.Item>
          <Form.Item label="–°–æ–¥–µ—Ä–∂–∞–Ω–∏–µ" name="content" rules={[{ required: true, message: '–í–≤–µ–¥–∏—Ç–µ —Ç–µ–∫—Å—Ç –Ω–æ–≤–æ—Å—Ç–∏' }]}>
            <Input.TextArea rows={4} />
          </Form.Item>

          {/* –ü–æ–ª–µ –¥–ª—è –∑–∞–≥—Ä—É–∑–∫–∏ –∫–∞—Ä—Ç–∏–Ω–∫–∏ */}
          <Form.Item label="–§–æ—Ç–æ (–æ–¥–Ω–æ)" name="image" valuePropName="fileList" getValueFromEvent={normFile}>
            <Upload
              listType="picture-card"
              maxCount={1}
              beforeUpload={() => false} // —á—Ç–æ–±—ã –Ω–µ –æ—Ç–ø—Ä–∞–≤–ª—è–ª–æ —Ñ–∞–π–ª –Ω–∞ —Å–µ—Ä–≤–µ—Ä –∞–≤—Ç–æ–º–∞—Ç–∏—á–µ—Å–∫–∏
            >
              <div>
                <PlusOutlined />
                <div style={{ marginTop: 8 }}>–ó–∞–≥—Ä—É–∑–∏—Ç—å</div>
              </div>
            </Upload>
          </Form.Item>
        </Form>
      </Modal>

      {/* –ú–æ–¥–∞–ª–∫–∞: –†–µ–¥–∞–∫—Ç–∏—Ä–æ–≤–∞–Ω–∏–µ */}
      <Modal
        title="–†–µ–¥–∞–∫—Ç–∏—Ä–æ–≤–∞—Ç—å –Ω–æ–≤–æ—Å—Ç—å"
        open={isEditModalOpen}
        onCancel={handleCancelEdit}
        onOk={() => editForm.submit()}
      >
        <Form form={editForm} layout="vertical" onFinish={handleEditFinish} name="editNewsForm">
          <Form.Item label="–ó–∞–≥–æ–ª–æ–≤–æ–∫" name="title" rules={[{ required: true, message: '–í–≤–µ–¥–∏—Ç–µ –∑–∞–≥–æ–ª–æ–≤–æ–∫ –Ω–æ–≤–æ—Å—Ç–∏' }]}>
            <Input />
          </Form.Item>
          <Form.Item label="–°–æ–¥–µ—Ä–∂–∞–Ω–∏–µ" name="content" rules={[{ required: true, message: '–í–≤–µ–¥–∏—Ç–µ —Ç–µ–∫—Å—Ç –Ω–æ–≤–æ—Å—Ç–∏' }]}>
            <Input.TextArea rows={4} />
          </Form.Item>

          <Form.Item label="–§–æ—Ç–æ (–æ–¥–Ω–æ)" name="image" valuePropName="fileList" getValueFromEvent={normFile}>
            <Upload listType="picture-card" maxCount={1} beforeUpload={() => false}>
              <div>
                <PlusOutlined />
                <div style={{ marginTop: 8 }}>–ó–∞–≥—Ä—É–∑–∏—Ç—å</div>
              </div>
            </Upload>
          </Form.Item>
        </Form>
      </Modal>
    </div>
  )
}
