// pages/DashboardPage/ui/DashboardPage.jsx
import React, { useEffect, useState } from 'react'

import { Row, Typography } from 'antd'

import { getAllCourses } from '../../../entities/course/model/courseStorage'
import { getAllSchedule } from '../../../entities/schedule/model/scheduleStorage'
import { getSession } from '../../../features/auth/model/session'
import DashboardCard from '../../../shared/ui/Card/Card'
import { NewsPage } from '../../NewsPage/ui/NewsPage'

const { Title, Text } = Typography

export const DashboardPage = () => {
  const [user, setUser] = useState(null)
  const [courses, setCourses] = useState([])
  const [schedule, setSchedule] = useState([])
  const [closestEvent, setClosestEvent] = useState(null)

  useEffect(() => {
    const sessionUser = getSession()
    if (sessionUser) {
      setUser(sessionUser)
    }

    // –ó–∞–≥—Ä—É–∑–∏–º –≤—Å–µ –∫—É—Ä—Å—ã
    const courseData = getAllCourses()
    setCourses(courseData)

    // –ó–∞–≥—Ä—É–∑–∏–º –≤—Å–µ —Å–æ–±—ã—Ç–∏—è —Ä–∞—Å–ø–∏—Å–∞–Ω–∏—è
    const scheduleData = getAllSchedule()
    setSchedule(scheduleData)

    // –ù–∞–π–¥—ë–º –±–ª–∏–∂–∞–π—à–µ–µ —Å–æ–±—ã—Ç–∏–µ (–ø–æ –¥–∞—Ç–µ/–≤—Ä–µ–º–µ–Ω–∏)
    if (scheduleData.length > 0) {
      const now = new Date()

      // –ü—Ä–µ–æ–±—Ä–∞–∑—É–µ–º "date" + "time" –≤ Date-–æ–±—ä–µ–∫—Ç
      // –∏ –Ω–∞–π–¥—ë–º —Å–∞–º–æ–µ –±–ª–∏–∂–∞–π—à–µ–µ, –∫–æ—Ç–æ—Ä–æ–µ –µ—â–µ –Ω–µ –ø—Ä–æ—à–ª–æ
      const upcomingEvents = scheduleData
        .map((ev) => {
          const [year, month, day] = ev.date.split('-')
          const [hours, minutes] = ev.time.split(':')
          const eventDate = new Date(
            +year,
            +month - 1, // –º–µ—Å—è—Ü—ã –≤ JS —Å 0
            +day,
            +hours,
            +minutes,
          )
          return { ...ev, eventDate }
        })
        .filter((ev) => ev.eventDate > now)
        .sort((a, b) => a.eventDate - b.eventDate)

      if (upcomingEvents.length > 0) {
        setClosestEvent(upcomingEvents[0])
      }
    }
  }, [])

  return (
    <div style={{ padding: '16px' }}>
      <Title level={2}>–î–æ–±—Ä–æ –ø–æ–∂–∞–ª–æ–≤–∞—Ç—å {user?.username}!</Title>
      <Text>
        –í–∞—à–∞ —Ä–æ–ª—å: <b>{user?.role === 'teacher' ? '–ü—Ä–µ–ø–æ–¥–∞–≤–∞—Ç–µ–ª—å' : '–°—Ç—É–¥–µ–Ω—Ç'}</b>
      </Text>

      <Row gutter={[16, 16]} style={{ marginTop: 16 }}>
        <DashboardCard title="–í—Å–µ–≥–æ –∫—É—Ä—Å–æ–≤:" value={courses.length} link="#/courses" linkText="–ü–µ—Ä–µ–π—Ç–∏ –∫ –∫—É—Ä—Å–∞–º" />
        <DashboardCard
          title="–†–∞—Å–ø–∏—Å–∞–Ω–∏–µ:"
          value={`${schedule.length} –∑–∞–Ω—è—Ç–∏–π`}
          extra={
            closestEvent ? (
              <div>
                <Text type="secondary">–ë–ª–∏–∂–∞–π—à–µ–µ –∑–∞–Ω—è—Ç–∏–µ:</Text>
                <div>
                  <Text>
                    {closestEvent.date} –≤ {closestEvent.time}, –∫—É—Ä—Å ID {closestEvent.courseId}
                  </Text>
                </div>
              </div>
            ) : (
              <Text type="secondary">–ë–ª–∏–∂–∞–π—à–∏—Ö –∑–∞–Ω—è—Ç–∏–π –Ω–µ –Ω–∞–π–¥–µ–Ω–æ.</Text>
            )
          }
          link="#/schedule"
          linkText="–ü–æ—Å–º–æ—Ç—Ä–µ—Ç—å —Ä–∞—Å–ø–∏—Å–∞–Ω–∏–µ"
        />
      </Row>
      <NewsPage />
    </div>
  )
}
